.card-lg {
    width:100%;
    padding:0;
    position: relative;
    -webkit-box-shadow: 1px 20px 1px -10px rgba(0,0,0,.2);
            box-shadow: 1px 20px 1px -10px rgba(0,0,0,.2);
}
.card-lg:hover {
    cursor: pointer;
}
.wrapper .card-lg .container {
    width: 80%;
    margin:0 auto;
    max-width: 980px;
    padding:0 20px;
    position: relative;
    z-index: 15;
}
.card-lg .bar {
    background: #0487d9;
    background-image: -webkit-linear-gradient(top, #0487d9, #006fb0);
    background-image: -o-linear-gradient(top, #0487d9, #006fb0);
    background-image: -webkit-gradient(linear, left top, left bottom, from(#0487d9), to(#006fb0));
    background-image: linear-gradient(to bottom, #0487d9, #006fb0);
    background-image: url('../../Assets/Images/portfolio-header.png');
    padding:20px 0;
}
.card-lg .bar:after {
    content:'';
    display:block;
    width:100%;
    clear:both;
}
.card-lg h5 {
    margin:0;
    color:#ffffff;
}
.card-lg h5.title {
    float:left;
}
.card-lg h5.type {
    float:right;
}
.card-lg .card-content {
    min-height: 370px;
    padding:20px 0;
    opacity: 0;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    position: relative;    
    color:#000000;
    overflow: hidden;
}
.card-lg .card-content .bg-overlay {
    display:block;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 5;
    -webkit-transform: scaleY(0);
        -ms-transform: scaleY(0);
            transform: scaleY(0);
    -webkit-transform-origin: 50%;
        -ms-transform-origin: 50%;
            transform-origin: 50%;
    -webkit-transition-property: -webkit-transform;
    transition-property: -webkit-transform;
    -o-transition-property: transform;
    transition-property: transform;
    transition-property: transform, -webkit-transform;
    -webkit-transition-duration: 0.3s;
         -o-transition-duration: 0.3s;
            transition-duration: 0.3s;
    -webkit-filter: blur(5px);
            filter: blur(5px);
    background-position: top center;
    background-repeat: no-repeat;
    background-size:cover;
}
.card-lg .card-content .bg-overlay::before {
    display: block;
    content:'';
    position: absolute;
    width: 100%;
    height: 100%;
    top:0;
    left:0;
    background:rgba(255,255,255, 0.8);
}
.card-lg:hover .card-content .bg-overlay {
    -webkit-transform: scaleY(1);
        -ms-transform: scaleY(1);
            transform: scaleY(1);
}
.card-lg:hover .card-content {
    opacity: 1;
}
.card-bg {
    border:1px solid #ccc;
    border-top:none;
}


