.wrapper.error {
    background:#ffffff;
    text-align: center;
}
.wrapper.error h2 {
    color:#D61118;
}
.wrapper.error p {
    padding:10px;
    border-radius:5px;
    background:#eeeeee;
    border:1px solid #dddddd;
    text-align: left;
    word-wrap: break-word;
    -webkit-animation: 0.3s fadeIn ease-in;
            animation: 0.3s fadeIn ease-in;
}

@-webkit-keyframes fadeIn{
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes fadeIn{
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}




