.notice {
    display:block;
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
    padding:5px 30px;
    border-radius: 5px;
    text-align: center;
    margin:0 auto 30px;
}
.notice.error {
    color: #a94442;
    background-color: #f2dede;
    border-color: #ebccd1;
}
.notice.error p {
    margin:0;
}


